export const checkColor = status => {
  if (status === '') {
    return '#56b858';
  } else if (status === '') {
    return '#b84956';
  } else if (status === '') {
    return '#6a92d4';
  } else if (status === ' ') {
    return '#d5db25';
  } else if (status === "'") {
    return '#eba946';
  } else if (status === ' ') {
    return '#84f59e';
  } else {
    return '#8a9ab5';
  }
};
